.forecast {
  font-size: .24rem;
  padding-bottom: 3.41667em;
}
.forecast-map {
  width: 100%;
  height: 40vh;
}
.forecast-detail {
  font-size: .2rem;
  background: #4f4fb5;
  color: #fff;
}
.forecast-region {
  line-height: 3;
  border-top: 1px solid rgba(127, 214, 195, 0.8);
  border-bottom: 1px solid rgba(127, 214, 195, 0.8);
  display: flex;
}
.expressway {
  border-right: 1px solid rgba(127, 214, 195, 0.8);
  flex-basis: 61%;
  display: flex;
  justify-content: center;
}
.county {
  flex: 1;
  display: flex;
  justify-content: center;
}
.forecast-region span {
  position: relative;
}
.forecast-region span::after {
  content: '';
  position: absolute;
  left: 100%;
  top: 0;
  width: 1em;
  height: 100%;
  margin-left: .5em;
  background: url(../images/icon-Dtriangle.png) center no-repeat/1em auto;
}
.forecast-panel {
  padding: 2.4em 1.2em 1.8em;
}
.forecast-panel .weather-temp {
  font-size: .24rem;
  padding: 0;
  padding-bottom: 1.667em;
  color: #fff600;
}
.forecast-panel .weather-temp .temp-number {
  font-size: .72rem;
}
.forecast-panel .weather-temp .temp-circle {
  width: .625em;
  height: .625em;
  border-width: .2em;
  border-color: #fff600;
}
.forecast-list {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.forecast-list-item {
  display: flex;
  align-items: center;
}
.forecast-list-item img {
  width: 1.5em;
  margin-right: .8em;
}
.forecast-list-item img.humidity {
  width: 1em;
}
.forecast-info {
  display: flex;
  flex-direction: column;
  align-items: center; 
}
.forecast-detail .list {
  width: 100%;
  position: relative;
}
.forecast-detail .list ul {
  width: 100%;
  background: #35367c;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 33;
}
.forecast-detail .list ul.county-list {
  text-align: right;
  padding-right: 5em;
  display: none;
}
.forecast-detail .list li {
  line-height: 3;
  display: flex;
  justify-content: flex-end;
}
.forecast-detail .list ul.county-list li.active span {
  position: relative;
}
.forecast-detail .list ul.county-list li.active span::before {
  content: '';
  position: absolute;
  right: 100%;
  top: 0;
  margin-right: 1.2em;
  width: 1.1em;
  height: 100%;
  background: url(../images/selected.png) center no-repeat/1.1em auto;
}
.forecast-temp {
  background: #6080e8 !important;
  position: relative;
}
.forecast-temp::before {
  content: '';
  position: absolute;
  bottom: 100%;
  left: calc(50% - .5em);
  width: 0;
  height: 0;
  border-width: .5em;
  border-style: dashed dashed solid dashed;
  border-color: transparent transparent #6080e8 transparent;
}
.forecast-nav {
  background: #35367c;
  border-top: 1px solid #afe6dd;
  width: 100%;
  height: 3.41667em;
  line-height: 3.41667em;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
}
.forecast-nav li a {
  display: inline-block;
  color: rgba(255, 255, 255, 0.6);
  padding: 0 .4em;
}
.forecast-nav li.on a {
  color: #46dada;
  position: relative;
}
.forecast-nav li.on a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: .25em;
  background: #46dada;
}
.predict {
  background: #6080e8;
  font-size: .2rem;
  line-height: 2;
  padding: .5em;
}
.predict-location {
  padding: 0 .8em .5em 1em;
  color: rgba(255, 255, 255, 0.6);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.predict-location > div {
  display: flex;
  align-items: center;
}
.predict-map {
  height: 30vh;
  min-height: 300px;
  border: 1px solid #7fd6c3;
  box-shadow: 0 1px 10px rgba(79, 79, 181, 0.7);
  margin-bottom: 1em;
}
.predict-list-item {
  background: transparent;
}
.predict-list-item.active {
  background: #5154ba;
  border: 1px solid #7fd6c3;
  box-shadow: 0 1px 10px rgba(79, 79, 181, 0.7);
  margin-bottom: .5em;
}
.predict-list-title {
  border-bottom: 1px solid #7a96f1;
  color: #7ad5c6;
  height: 3em;
  padding: .5em 0 .5em 3em;
  transition: all 0.3s;
  background-repeat: no-repeat;
  background-size: 1.5em 1.5em;
  background-position: left 1em center;
  position: relative;
  display: flex;
  align-items: center;
}
.predict-list-title::after {
  content: '';
  position: absolute;
  right: .8em;
  top: 0;
  width: .8em;
  height: 100%;
  transition: all .3s;
  background-repeat: no-repeat;
  background-position: center;
  background-size: .8em auto;
  background-image: url(../images/predict-Dtriangle.png);
}
.predict-list-item.active .predict-list-title::after {
  background-image: url(../images/predict-Utriangle.png);
}
.predict-list-title span {
  display: inline-block;
  width: 6em;
}
.predict-list-title b {
  font-size: .24rem;
  color: #fff;
}
.predict-temperature-highest {
  background-image: url(../images/temperature-highest-unselected.png);
}
.predict-temperature-lowest {
  background-image: url(../images/temperature-lowest-unselected.png);
}
.predict-visibility {
  background-image: url(../images/visibility-unselected.png);
}
.predict-wind-direction {
  background-image: url(../images/wind-direction-unselected.png);
}
.predict-rainfall {
  background-image: url(../images/rainfall-unselected.png);
}
.predict-humidity {
  background-image: url(../images/humidity-unselected.png);
}
.predict-list-item.active .predict-list-title {
  color: #fff;
  border-color: #3c6cb3;
}
.predict-list-item.active .predict-temperature-highest {
  background-image: url(../images/temperature-highest.png);
}
.predict-list-item.active .predict-temperature-lowest {
  background-image: url(../images/temperature-lowest.png);
}
.predict-list-item.active .predict-visibility {
  background-image: url(../images/visibility.png);
}
.predict-list-item.active .predict-wind-direction {
  background-image: url(../images/wind-direction.png);
}
.predict-list-item.active .predict-rainfall {
  background-image: url(../images/rainfall.png);
}
.predict-list-item.active .predict-humidity {
  background-image: url(../images/humidity.png);
}
.predict-list-content {
  display: block;
}
.predict-list-item.active .predict-list-content {
  display: block;
}


